body.dark-theme {
  background-color: #333333;
  color: #fff;
}

/* THEME SWITCH */
.theme-switch {
  display: flex;
  align-items: baseline;
  justify-content: flex-end;
  background-color: white;
  border-radius: 20px;
  position: absolute;
  right: 20px;
  top: 200px;

  @media screen and (min-width: 768px) {
    right: 70px;
}
}

.theme-switch__icon {
  color: currentColor;
  height: 15px;
  width: 15px;
  margin-right: 3px;
  margin-left: 3px;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 25px;
  height: 15px;
  border-radius: 50em;
  padding: 3px 0;
}

.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}

.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: #333;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}

.theme-switch__marker {
  position: relative;
  background-color: #fff;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  transform: translateX(1px);
  transition: transform 250ms var(--timing-function);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(10px);
}